body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.app {
  box-sizing: border-box;
}
.app .miniApp {
  width: 400px;
  height: 80vh;
}
@media screen and (max-width: 670px) {
  .app .miniApp {
    width: 100vw;
    height: 100vh;
  }
}
.app .Main {
  padding: 20px;
  background: gainsboro;
  position: relative;
  height: 100%;
}
.app .Main .rmc-notification {
  position: absolute;
  top: unset !important;
  bottom: 20px;
}
.app .AccountList .Account {
  margin-top: 12px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 5px 0px;
  background-color: #ffffff;
  padding: 14px 14px 0;
}
.app .AccountList .Account .Account-panel {
  display: flex;
  flex-direction: column;
}
.app .AccountList .Account .Account-panel .code-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.app .AccountList .Account .Account-panel .code-row .tfa-code {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: -webkit-transform 200ms linear;
  transition: -webkit-transform 200ms linear;
  transition: transform 200ms linear;
  transition: transform 200ms linear, -webkit-transform 200ms linear;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.app .AccountList .Account .Account-panel .code-row .tfa-code:active {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}
.app .AccountList .Account.noTimeToDo .tfa-code {
  color: #a6a6a6;
}
.app .SearchBox {
  margin-top: 12px;
}
.app .AddForm {
  margin-top: 12px;
}
.app .AddForm .addButton {
  margin-top: 12px;
}
.app .AddForm .addButton .ms-Button {
  width: 100%;
}
.app .SettingForm .sync-btn {
  padding-top: 12px;
  display: flex;
}
.app .SettingForm .sync-btn .ms-Button {
  flex: 1 1;
}
.app .SettingForm .sync-btn .ms-Spinner {
  margin-left: 6px;
  margin-right: 6px;
  flex: 0 0 auto;
}
.app .SettingForm .user-sync-btn {
  padding-top: 12px;
  display: flex;
  flex-direction: column;
}
.app .SettingForm .user-sync-btn .ms-Button {
  flex: 1 1;
}
.app .SettingForm .user-sync-btn .time-set-div {
  margin-top: 20px;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1), 0 0 4px 0 rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: white;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233 {
  display: none;
  box-shadow: none;
  padding: 0;
  width: 100%;
  text-align: center;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233 .picky-date-time__close {
  display: none;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233 .picky-date-time-calendar__button {
  position: unset;
  width: 90%;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233 .picky-date-time__calendar {
  margin-bottom: 0;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233 .picky-date-time__clock {
  margin-bottom: 0;
  padding-bottom: 12px;
}
.app .SettingForm .user-sync-btn .time-set-div .picky-date-time233.visible {
  display: block;
}
.app .SettingForm .user-sync-btn .time-set-div .ms-Button {
  flex: unset;
}
.app .NowTime .systemTime,
.app .NowTime .usedTime {
  margin-top: 12px;
  background-color: #f4f4f4;
  padding: 0 20px;
}
.app .NowTime .usedTime .ms-Label {
  color: #0078d4;
}
